<div class="main-info" v-if="tv"> <img :src="imageFullUrl + tv.backdrop_path" v-if="tv.backdrop+path"><img src="@/assets/img/nophoto.png" alt="alt" v-else><div class="main-info-block"> <div class="main-info-content"><div class="main-info-content-block"><h2>{{tv.name}}   </h2><p>{{tv.overview || 'Описание отсутствует...'}}</p><p class="date"><span>{{new Date (tv.first_air_date).getFullYear()}},</span><span v-for="genre in tv.genres" :key="genre.id">{{genre.name}}</span></p><div class="actors"><Actors :type="'/tv/'" :id="tv.id" count="6"></Actors></div></div></div></div></div>